import { useCallback, useEffect, useMemo, useState } from "react"
import { DOMAIN_NAME } from "../../actions/api";
import { GET_COOKIE, SET_COOKIES } from "../../utils";
import { FontAwesomeIcon } from "../../Assets/AssetsHelper";
import { useDispatch } from "react-redux";
import { USER_COOKIE_CONSENT } from "../../reducer/keys";

const CookieConsent = () => {
    const [showConsent, setShowConsent] = useState(null);
    const dispatch = useDispatch();

    const handleConsent = useCallback((consentType) => {
        setShowConsent(false);
        sendPermission(consentType);
        SET_COOKIES("cookieAllowed", consentType ? "allowed" : "not-allowed");
    }, [])

    const sendPermission = useCallback((permission) => {
        if (window.clarity) {
            window.clarity('consentv2', {
                ad_Storage: permission ? "granted" : "denied",
                analytics_Storage: permission ? "granted" : "denied"
            });
        }
        dispatch({ type: USER_COOKIE_CONSENT, payload: permission })
    }, [dispatch])

    useEffect(() => {
        const isAllowed = GET_COOKIE("cookieAllowed");
        if (isAllowed != null) {
            setShowConsent(false);
            sendPermission(isAllowed == "allowed");
        } else setShowConsent(true);
    }, [])

    return showConsent ? <div className="position-fixed start-50 bottom-0 translate-middle-x px-2 pb-1 cookie-alert" style={{zIndex:'99'}}>
        <div className="cookie-wrap d-flex align-items-center bg-dark rounded-2 p-2">
            <div className="cookie-text d-flex align-items-start gap-1">
                <FontAwesomeIcon iconClass="mt-1 text-white" icon="cookie-bite" />
                <p className="mb-0 text-white" style={{ fontSize: "12px" }}>
                    This app uses cookies for functionality, analytics and personalization. See our
                    <a className="ms-1" href={`https://${DOMAIN_NAME}.com/privacy-policy`} target="_blank">Privacy Policy</a>
                </p>
            </div>
            <div className="d-flex align-items-center gap-2 cookie-btns">
                <button className="btn btn-sm btn-light p-1" style={{ fontSize: "10px" }} onClick={() => handleConsent(false)}>Reject</button>
                <button className="btn btn-sm btn-primary p-1" style={{ fontSize: "10px" }} onClick={() => handleConsent(true)}>Accept</button>
            </div>
        </div>
    </div> : null
}

export default CookieConsent;